            <p class="kongxi"></p>
            <p></p>
            <div class="daima">
                <pre>function Super(val){
    this.val = val;//
    this.arr = [1];
    this.fun = function(){
   }
  }
  function Sub(val){
      Super.call(this,val);//
      //call()apply()
  }
  var sub1 = new Sub(1);
  var sub2 = new Sub(2);
  sub1.arr.push(2);
  alert(sub1.val);//1
